import React from "react";
import styles from "../styles/about.css";
console.log(styles);

import Container from "../components/container";

const pStyle = {
    textAlign: 'justify'
};

const User = props => 
    <div className={styles.user}>
        <div className={styles.description}>
            <h2 className={styles.username}>
                {props.username}
            </h2>
            <p className={styles.excerpt}>
                {props.excerpt}
            </p>
        </div>
    </div>
export default ({ data }) => (
    <Container>
        <h1>About {data.site.siteMetadata.title}</h1>
        <p style = {pStyle} >Este sitio pretende ser una herramienta y bitacora, para dar seguimiento a la clase de computación blanda 
            dictada por el profesor Gilberto Vargas Cano en la Universidad Tecnológica de Pereira, en el
            segundo semestre del año 2018.
            Dentro de este blog se publicaran Papers y codigos de aplicaciones en el campo de la Inteligencia Artificial 
        </p>
        <User
            username={data.site.siteMetadata.author}
            avatar="https://s3.amazonaws.com/uifaces/faces/twitter/adellecharles/128.jpg"
            excerpt="Estudiante de Ingenieria en Sistemas y Computación, apasionado por las ciencias de la computación y las ciencias exactas"
        />
    </Container>
);

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
        author
      }
    }
  }
`



